<template>
    <div>
        <div class="bg-primary text-white p-3 mb-3">订单管理</div>
        <!-- Filters -->
        <div>
            <div class="align-middle pt-2 pb-2 mb-3 bg-secondary text-light">
                <div class="row input-group">
                    <div class="col-2 offset-4">
                        <input type="datetime-local" class="form-control" title="起始时间"
                               v-model="filters.time.start" v-bind:disabled="!filters.time.enabled"/>
                    </div>
                    <span class="col-1 m-auto">到</span>
                    <div class="col-2">
                        <input type="datetime-local" class="form-control" title="结束时间"
                               v-model="filters.time.end" v-bind:disabled="!filters.time.enabled"/>
                    </div>
                    <div class="col-1 m-auto">
                        启用该选项
                    </div>
                    <div class="col-1">
                        <input type="checkbox" value="time_filter_enabled" class="form-control"
                               v-model="filters.time.enabled"/>
                    </div>
                </div>
                <hr/>
                <button class="btn btn-block btn-info col-8 offset-2" v-on:click="requestOrderList()">
                    查询
                </button>
                <div v-if="!gettingOrderList&&!getOrderListFail" class="text-center mt-3">
                    共 {{ orders.length }} 条结果
                </div>
            </div>
            <!--div class="input-group bg-secondary pt-2 pb-2 mb-3">
                <input type="text" class="form-control col-2 offset-9" placeholder="按订单号搜索"
                       v-model.number="filters.id"/>
                <button class="btn btn-info col-1">查询</button>
            </--div>
            <div v-if="admin" class="input-group bg-secondary pt-2 pb-2">
                <input type="text" class="form-control col-2 offset-9" placeholder="按用户UID搜索"
                       v-model.number="filters.uid"/>
                <button class="btn btn-info col-1">查询</button>
            </div-->
        </div>
        <!-- Table -->
        <div v-if="gettingOrderList">获取订单信息中……</div>
        <div v-else-if="getOrderListFail">获取订单信息失败……</div>
        <div v-else>
            <table class="table">
                <thead>
                <tr>
                    <th>时间</th>
                    <th>买家</th>
                    <th>购买项</th>
                    <th>总价</th>
                </tr>
                </thead>
                <tbody>
                <div v-for="(order, index) in orders" v-bind:key="index">
                    <OrderItem v-bind:order="order"></OrderItem>
                </div>
                </tbody>
            </table>
        </div>
    </div>
</template>

<script>
    import OrderItem from '@/components/OrderItem';
    export default {
        name: 'OrderList',
        components: {OrderItem},
        props: {
            admin: {
                default: false
            }
        },
        created: function () {
            this.requestOrderList();
        },
        methods: {
            requestOrderList: function () {
                this.gettingOrderList = true;
                this.$http.post(
                    'order', this.filters
                ).then(
                    (resp)=>{
                        this.orders = resp.data.orders;
                        this.getOrderListFail = false;
                        this.gettingOrderList = false;
                    }, ()=>{
                        this.getOrderListFail = true;
                        this.gettingOrderList = false;
                    }
                );
            }
        },
        data: function () {
            return {
                filters: {
                    id: '',
                    time: {
                        enabled: false,
                        start: new Date(),
                        end: new Date()
                    }
                },
                orders: [],
                gettingOrderList: false,
                getOrderListFail: false
            };
        }
    }
</script>

<style scoped>

</style>
